      #t_4_1.titulo-segundo.color-acento-botones(data-aos="fade-right")  
        h2 4.1 Grfica digital

      .row.justify-content-center.mb-5
        .col-lg-8(data-aos="fade-left") 
          .squareTop.mb-4
            p.mb-0 Analizando lo que es la grfica digital se va a diferenciar primero que todo, qu es el diseo grfico y la grfica digital. El diseo grfico en esencia hace referencia a una serie de funciones y tcnicas implementadas en la creacin de piezas grficas, dirigidas a los medios tradicionales, como por ejemplo, a medios impresos como catlogos, folletos, revistas, afiches, vallas publicitarias, etc.
          .row
            .col-auto.d-none.d-lg-flex
              figure
                img(src="@/assets/curso/temas/recursos/square_01.svg", alt="alt").img30
            .col.mb-4.mb-lg-0
              p.mb-0 Mediante un proceso estratgico y creativo se crean campaas publicitarias con el fin de dar a conocer la identidad de una empresa o presentar un producto o servicio. La grfica digital hace parte de estos procesos, pero la diferencia es que no va dirigida a los medios tradicionales, si no que va dirigida a los medios digitales como Internet, sitios #[em web], redes sociales, videojuegos, etc.
          
        .col-lg-4.col-8(data-aos="zoom-in") 
          figure
            img(src="@/assets/curso/temas/t4/t4_02.svg", alt="alt")

        p(data-aos="fade-left").mt-4.mb-0 Hoy en da la tecnologa ofrece muchas opciones para desarrollar imgenes digitales, ya sean en formatos BMP, GIF, JPG, TIF y PNG. Tambin se pueden desarrollar documentos, presentaciones o hasta un libro en formatos digitales como archivos PDF.

    h5(data-aos="fade-right").mb-5 A continuacin, las principales caractersticas:

    .row.justify-content-center
      .col-lg-5.col-8.mb-4.mb-lg-0(data-aos="zoom-in")
        figure
          img(src="@/assets/curso/temas/t4/t4_03.svg", alt="alt")
      .col-lg-7(data-aos="fade-right")
        LineaTiempoD.color-acento-botones.mb-5
          .row(numero="1" titulo="Inmediatez")
            .col
              p.mb-0 Al vivir en un mundo tan acelerado se recibe todos los das un sinfn de datos de forma simultnea, y eso justamente es una de las grandes caractersticas de la comunicacin digital, que permite recoger informacin de manera inmediata y sin necesidad de esperar al siguiente da para tener un contenido en la mano.

          .row(numero="2" titulo="Multimedialidad")
            .col
              p.mb-0 Si bien los medios tradicionales como la prensa, la radio y la televisin son los principales canales de comunicacin para llegar a una comunidad de masas, con la aparicin de la comunicacin digital se puede acceder a contenidos valiosos en tiempo real, a travs de mltiples canales en lnea. Esta caracterstica de la comunicacin digital rene diversos medios que estn en sintona en un mismo sitio.

          .row(numero="3" titulo="Interactividad")
            .col
              p.mb-0 Otra de las caractersticas de la comunicacin digital es la participacin de los usuarios. La fcil accesibilidad a los medios #[em online] permite que los clientes puedan interactuar con su marca a travs de las redes sociales, los foros, las pginas #[em web], etc.

          .row(numero="4" titulo="Actualizacin en tiempo real")
            .col
              p.mb-0 Un error de redaccin u ortografa en un peridico impreso podra generar una interpretacin diferente por parte de los lectores, gracias a esta caracterstica de la comunicacin digital puede editar cualquier contenido en tiempo real y actualizarlo con nuevos datos valiosos.
              .cajon.cjn--grayBlue.p-4.mb-0
                p.mb-0 Por eso, tenga mucho cuidado al momento de publicar contenidos en sus canales de comunicacin, pues, as como puede actualizarlos inmediatamente, tambin puede recibir crticas por parte de los usuarios si su informacin no est respaldada por fuentes confiables.
              
          .row(numero="5" titulo="Multidireccionalidad")
            .col
              p.mb-0 Esta caracterstica de la comunicacin digital propone que el emisor pueda ser al mismo tiempo receptor y viceversa. Qued atrs el receptor pasivo! Con una comunicacin multidireccional el usuario se convierte en un agente activo que no solo participa de los mensajes, sino tambin los genera a travs de las diversas plataformas.

    separador

    #t_4_2.titulo-segundo.color-acento-botones(data-aos="fade-right") 
      h2 4.2 Video
    
    figure(data-aos="zoom-in")
      img(src="@/assets/curso/temas/t4/t4_04.png", alt="alt")
    .row.justify-content-center.mb-5.mb-lg-0(data-aos="fade-bottom")
      .col-lg-10
        .cstmTitleCrd
          p.mb-0 Se define video a la accin de capturar, grabar una imagen o sonido, en un medio magntico o digital, que posteriormente se puede reproducir o verse en una pantalla digital. Actualmente, existen varios formatos de captura, los cuales se definirn a continuacin, entendiendo cada formato de video, con el fin de que las personas los puedan visualizar de la mejor manera y elegir el formato de archivo de video adecuado.

    .bg_img_03(data-aos="fade-right")
      .row.mb-4
        .col-auto
          h3.custom-H3 Formatos de salida

      .row.justify-content-center.align-items-center.mb-5
        .col-lg-8.mb-4.mb-lg-0(data-aos="fade-left")
          p As como los textos y las imgenes, los videos tambin tienen sus extensiones y formatos digitales.
          .squareTop
            p.mb-4 En la realizacin de videos por lo general se configuran las cmaras en resoluciones de alta calidad, a medida que se realizan capturas aumenta el tamao de un archivo de video, sumado a esto, los videos pasan por un proceso de edicin en el que se agregan o importan elementos digitales como imgenes, mens, textos, efectos sonoros y todo lo que se requiera para su finalizacin. Esto hace que los videos aumenten de tamao.
          .cajon.cjn--grayBlue.p-4
            p.mb-0 Siempre es importante tener claro el objetivo de lo que se busca hacer, determinar cul es el fin del video y su publicacin, as se tendr una idea ms clara acerca del formato a utilizar.
        .col-lg-4.col-8(data-aos="zoom-in")
          figure
            img(src="@/assets/curso/temas/t4/t4_05.svg", alt="alt")

      .row.align-items-center.mb-4(data-aos="zoom-in")
        .col-auto.d-none.d-lg-flex
          figure
            img(src="@/assets/curso/temas/recursos/square_01.svg", alt="alt").img30
        .col.mb-4.mb-lg-0
          p.mb-0 Los formatos de video se refieren a su codificacin, ya que en el video digital al igual que pasa con un programa de computador, cualquier archivo est escrito en un cdigo determinado. A continuacin, se observar los formatos de video digital ms conocidos en el momento, cul es su origen y cules son sus presentaciones:

    .slyderBg_01.py-5.p-4.mb-5(data-aos="zoom-in")
      SlyderA(tipo="b")
        .row.justify-content-around.align-items-center
          .col-lg-8.mb-2.mb-lg-0
            h3 Format AVI
            p.mb-0 El formato AVI es uno de los formatos ms conocidos. El archivo de video con extensin .AVI fue lanzado en 1992 y es tan conocido que lo reproducen la mayora de televisores inteligentes, reproductores DVD/Blu-Ray, consolas de videojuegos y sistemas operativos. Entre sus ventajas, permite incluir varios canales de audio y alojar contenido generado con distintos cdec (AC3 o MP3 para audio, DivX o Xvid para video).       
          .col-lg-3.col-8
            figure
              img(src='@/assets/curso/temas/t4/t4_06.svg', alt='').img120.m-auto

        .row.justify-content-around.align-items-center
          .col-lg-8.mb-2.mb-lg-0
            h3 Format MP4
            p.mb-0 Es uno de los formatos ms actuales, lanzado en 1998 como estndar para reproducir video y audio en un nico archivo digital. Este formato de video es uno de los formatos ms utilizados en el momento para compartir contenido en lnea, YouTube recomienda cargar archivos en formato .mp4 para obtener la mejor calidad de video. Los videos que usan la extensin .MP4 pueden tener tamaos de archivo relativamente pequeos, mientras conservan una alta calidad.     
          .col-lg-3.col-8
            figure
              img(src='@/assets/curso/temas/t4/t4_07.svg', alt='').img120.m-auto

        .row.justify-content-around.align-items-center
          .col-lg-8.mb-2.mb-lg-0
            h3 Format H264
            p.mb-0 Es el formato de video ms usado, conocido como MPEG-4, usa compresin con prdida y es un formato universal de video de uso comn en la actualidad. El cdec es ampliamente compatible y se utiliza en la produccin, publicacin y distribucin de video. Es un formato que est incorporado en videocmaras y DSLR graban en H.264. Es el estndar para discos Blu-ray, as como para muchos servidores de #[em videoweb]. H.264 es ms eficiente para la compresin que MPEG-2 y normalmente ofrece una mejor calidad de video con la misma tasa de #[em bits].     
          .col-lg-3.col-8
            figure
              img(src='@/assets/curso/temas/t4/t4_08.svg', alt='').img120.m-auto

        .row.justify-content-around.align-items-center
          .col-lg-8.mb-2.mb-lg-0
            h3 Format MOV
            p.mb-0 Dentro de los tipos de formato de video para Apple es el mejor. Apple desarroll el contenedor .MOV para usar con su reproductor Quicktime. Los videos que usan .MOV generalmente tienen una calidad muy alta, pero tambin tamaos de archivo bastante grandes. Los videos de Quicktime no tienen tanta compatibilidad con los reproductores que no son de Quicktime, aunque hay reproductores de terceros que los leern. Este formato es til para guardar el archivo de un video de alta calidad en una computadora Apple.    
          .col-lg-3.col-8
            figure
              img(src='@/assets/curso/temas/t4/t4_09.svg', alt='').img120.m-auto

        .row.justify-content-around.align-items-center
          .col-lg-8.mb-2.mb-lg-0
            h3 Format WMV
            p.mb-0 Los videos de Windows Media tienden a tener el tamao de archivo ms pequeo, lo que puede ser una buena opcin para compartirlos por correo electrnico u otros mtodos con lmites de tamao de archivo. Cabe decir que, en su compresin se pierde calidad. Un uso comn de .WMV es enviar vistas previas de videos por correo electrnico a los clientes. Windows Media Video es un formato compatible con PowerPoint, por ser un formato de Microsoft.   
          .col-lg-3.col-8
            figure
              img(src='@/assets/curso/temas/t4/t4_10.svg', alt='').img120.m-auto

        .row.justify-content-around.align-items-center
          .col-lg-8.mb-2.mb-lg-0
            h3 Format XVID
            p.mb-0 Cuando el Divx dej de ser tan popular dio paso al XviD superando a este por su mejor calidad, hacindose ms famoso porque era soportado por muchos reproductores de DVD. En la actualidad, aunque hay otro tipo de formato de video ms potente se sigue utilizando esta compresin, ya que su rendimiento y velocidad de compresin es muy buena.    
          .col-lg-3.col-8
            figure
              img(src='@/assets/curso/temas/t4/t4_11.svg', alt='').img120.m-auto

        .row.justify-content-around.align-items-center
          .col-lg-8.mb-2.mb-lg-0
            h3 Format RM
            p.mb-0 RM es un archivo multimedia guardado en el formato de contenedor multimedia RealMedia desarrollado por RealNetworks. Puede almacenar solo audio en formato RealAudio, solo video en formato RealVideo, o tanto RealAudio como RealVideo. Los archivos RM se utilizan comnmente para transmitir contenido a travs de Internet, como video y radio por Internet.
            p.mb-0 El reproductor Real Player utiliza el formato RM para reproducir archivos de audio y video en todos los sistemas operativos, incluidos Linux, Mac y Windows.    
          .col-lg-3.col-8
            figure
              img(src='@/assets/curso/temas/t4/t4_12.svg', alt='').img120.m-auto

    .bg_img_03(data-aos="fade-right")
      .row.mb-4
        .col-auto
          h3.custom-H3 Caractersticas del video

    .row.justify-content-center.mb-5
      .col-lg-4.col-8.mb-4.mb-lg-0(data-aos="zoom-in")
        figure
          img(src='@/assets/curso/temas/t4/t4_13.svg', alt='')
      .col-lg-8(data-aos="fade-right")
        .row.mb-4
          .col-auto.d-none.d-lg-flex
            figure
              img(src="@/assets/curso/temas/recursos/square_01.svg", alt="alt").img30
          .col.mb-4.mb-lg-0
            p.mb-0 Es importante cuando se habla de video tener en cuenta las siguientes caractersticas para su realizacin. Los formatos de video han ido cambiando con la evolucin de la tecnologa, cuando aparecieron los primeros formatos de video digital a finales de los aos 80 solamente existan dos criterios: el tamao de imagen (ancho x alto) y el #[em frame rate] (imgenes por segundo que hay en el formato), a los dos criterios se uni un tercero con la aparicin de los formatos de grabacin digital, el aspecto de pixel (PAR).
        .cajon.cjn--grayBlue.p-4
          p.mb-0 En la actualidad un formato de video digital est constituido por estas 3 caractersticas:

    .row.justify-content-center.mb-5
      .col-lg-4.col-8.mb-4.mb-lg-0(data-aos="flip-left")
        .crd.bg--paleBlue.brad_01.hg-100.p-4
          figure.mb-3
            img(src="@/assets/curso/temas/t4/t4_14.svg", alt="alt").img75
          p.mb-0 El ancho y el alto (el tamao de imagen).

      .col-lg-4.col-8.mb-4.mb-lg-0(data-aos="flip-left")
        .crd.bg--paleYellow.brad_01.hg-100.p-4
          figure.mb-3
            img(src="@/assets/curso/temas/t4/t4_15.svg", alt="alt").img75
          p.mb-0 La cantidad de imgenes por segundo que tiene el formato (#[em frame rate]). 

      .col-lg-4.col-8.mb-4.mb-lg-0(data-aos="flip-left")
        .crd.bg--paleGray.brad_01.hg-100.p-4
          figure.mb-3
            img(src="@/assets/curso/temas/t4/t4_16.svg", alt="alt").img75
          p.mb-0 El aspecto de pixel (P.A.R.). 

    .row.justify-content-center.align-items-center.mb-5 
      .col-lg-8.mb-4.mb-lg-0(data-aos="fade-right")
        .squareTop.mb-4
          p.mb-0 Adems, la televisin tiene compatibilidad, es decir, que lo que grabe se tiene que poder ver en una televisin antigua. Esto provoca que a medida que van apareciendo nuevos formatos de video se tenga que mantener esa compatibilidad y para ello, son necesarias las nuevas tecnologas. Todo esto provoca que en la actualidad se tenga un gran conjunto de formatos de video.
        .cajon.cjn--grayBlue.p-4.mb-0
          p.mb-0 El tamao de los formatos de video digital est medido en pxeles y se representa con las coordenadas x, y, que se corresponden con el ancho y el alto. Con el tamao de imagen se puede saber si estamos ante una resolucin estndar, alta definicin, formatos de cine, en ultra alta definicin, etc.
      .col-lg-4.col-10(data-aos="zoom-in")
        figure
          img(src='@/assets/curso/temas/t4/t4_17.svg', alt='')

    .row.justify-content-center(data-aos="zoom-in")
      .col-lg-10
        .row.justify-content-center.align-items-lg-center.mb-5
          .col-auto.avtr-txt
            figure
              img(src="@/assets/curso/temas/recursos/icon_01.png", alt="alt").img150
          .col
            p.mb-0 #[strong Se debe tener en cuenta el tamao de imagen a la hora de subir un video a YouTube, cuando se va a proyectar un video en un proyector, si se va a proyectar en una sala de cine, etc., para poder cumplir con los requisitos de tamao de imagen del dispositivo donde se vaya a proyectar. A su vez, se debe tener en cuenta lo siguiente:]

    .row.justify-content-center.mb-5
      .col-lg-5.col-8.mb-4.mb-lg-0(data-aos="zoom-in")
        figure
          img(src="@/assets/curso/temas/t4/t4_18.svg", alt="alt")
      .col-lg-7(data-aos="fade-right")
        LineaTiempoD.color-acento-botones.mb-5
          .row(numero="1" titulo="<em>Frame rate</em>")
            .col
              p La frecuencia o cadencia de imagen (#[em frame rate]), es decir, la cantidad de fotogramas o frames que hay en un segundo para dar la sensacin de movimiento, esto es, el nmero de imgenes fijas necesarias para crear un segundo de video.
              .cajon.cjn--grayBlue.p-4
                p.mb-0 El #[em frame rate] es muy importante porque  va a permitir saber ante qu formatos de video se encuentran, se puede saber si est trabajando para un formato de cine, un formato de ultra alta, etc. El #[em frame rate] es una de las caractersticas principales de los formatos de video.

          .row(numero="2" titulo="El aspecto de pxel")
            .col
              p Cuando se trata del aspecto del pxel, el concepto pxel aparece con los formatos de grabacin digital. Los pxeles siempre haban sido cuadrados, pero a partir de la aparicin de este concepto, los pxeles comienzan a ser rectangulares.
              p Se hace referencia al aspecto de pxel a la deformacin del aspecto del video para mantener constante el ancho del formato de video, aunque se cambie la cantidad de nmeros de pxeles que hay en horizontal.

              .cajon.cjn--grayBlue.p-4.mb-4
                p.mb-0 Para entender con mejor facilidad se puede poner como ejemplo el sistema PAL, el cual tiene un tamao de imagen de 788 x 576 (ancho x alto). Pero con la aparicin de los formatos de grabacin digital el ancho pasa a ser de 720, es decir, se tiene menos pxeles a lo ancho.

              p.mb-0 #[strong Cmo se hace esto?] <br>La compatibilidad de la televisin no permite trabajar un formato de video ms estrecho, para ello se debe estirar el pxel con el objetivo de que menos pxeles ocupen lo mismo que ms pxeles (se ensancha el aspecto del pxel).

          .row(numero="3" titulo="Optimizacin")
            .col
              p.mb-0 Actualmente el #[em marketing] digital es sin duda, un medio que las empresas utilizan para presentar sus estrategias, tener presente una serie de recomendaciones a la hora de implementar videos en los medios digitales ayudar a una mejor optimizacin. 

          .row(numero="4" titulo="Visualizaciones")
            .col
              p.mb-0 Aqu es importante realizar seguimiento en tiempo real para analizar el nmero de visualizaciones que tiene el video, esto ayudar a determinar qu tanto inters ha despertado en las personas. Este anlisis tambin sirve para mirar si el video tiene visualizaciones ms pronto. Se recomienda observar visualizaciones en horarios diferentes.

    .row.justify-content-center.mb-5      
      .col-lg-7.mb-4.mb-lg-0(data-aos="fade-right")
        LineaTiempoD.color-acento-botones.mb-5
          .row(numero="5" titulo="Tiempo de reproduccin")
            .col
              p.mb-0 Es importante, un video muy extenso puede hacer que se pierda el inters, optimizar una reproduccin corta es la mejor opcin. Los minutos totales de visualizacin de los contenidos son ms importantes que el propio dato de las visualizaciones sin procesar. Los videos de menos de 5 minutos captan un 50% ms la audiencia.

          .row(numero="6" titulo="Ubicacin de reproduccin")
            .col
              p.mb-0 Identificar las tendencias de la audiencia, conocer en dnde prefieren ver los videos, determinar dnde estn reproduciendo los videos puede ayudar a entender, es decir, buscan los videos desde algn canal como es YouTube o a partir de diferentes #[em web]?

          .row(numero="7" titulo="Audiencia")
            .col
              p.mb-0 Definir, determinar el grupo objetivo, es importante saber quin est observando los videos y tener estos datos organizados por edad, sexo, geografa o dispositivo. Con esta revisin de la audiencia se puede obtener informacin para la creacin de prximos videos para una edad o ubicacin especfica.
              
          .row(numero="8" titulo="Interacciones sociales")
            .col
              p.mb-0 Las redes sociales y aplicaciones tienen una importancia, el rendimiento de los videos. Analizar quin est compartiendo el contenido y cmo lo est haciendo. Hay que hacer un seguimiento de los recursos compartidos que ha recibido cada video a lo largo del tiempo en las distintas plataformas sociales. Los comentarios son otro punto clave. Controlar quin est comentando y lo que dicen puede ayudar a aumentar la participacin del video u otro contenido. Es necesario ver la frecuencia y las fechas de los comentarios, as como conocer la cantidad total de los mismos en cada video. 

      .col-lg-5.col-8(data-aos="zoom-in")
        figure
          img(src="@/assets/curso/temas/t4/t4_19.svg", alt="alt")

    .bg_img_03(data-aos="fade-right")
      .row.mb-4
        .col-auto
          h3.custom-H3 Herramientas de seguimiento

    p(data-aos="fade-left") Aqu se describen todas las mtricas que se han comentado anteriormente y que se pueden monitorizar a travs de distintas herramientas, para ayudar a su anlisis y saber si se acercan o alejan de los objetivos planteados. Estas son algunas de las herramientas:

    .slyderBg_01.pt-5.p-4(data-aos="zoom-in").mb-5
      SlyderA(tipo="b")
        .row.justify-content-around.align-items-center
          .col-lg-3.col-8.mb-2.mb-lg-0
            figure
              img(src='@/assets/curso/temas/t4/t4_20.png', alt='')
          .col-lg-8
            h4 YouTube Analytics
            p.mb-0 Si los videos estn subidos a esta plataforma es el mejor sitio para iniciar con un anlisis en profundidad de forma gratuita. Con esta herramienta se pueden realizar revisiones estadsticas para toda la informacin comentada en el #[em post] y mucho ms.

        .row.justify-content-around.align-items-center
          .col-lg-3.col-8.mb-2.mb-lg-0
            figure
              img(src='@/assets/curso/temas/t4/t4_21.png', alt='')
          .col-lg-8
            h4 Google Analytics 
            p.mb-0 Esta herramienta permite rastrear la interaccin de la audiencia con los videos integrados en sitios #[em web]. Se puede conocer si el pblico reproduce los videos hasta realizar un seguimiento de la frecuencia con la que los visitantes los ven hasta el final. Incluso se pueden rastrear las pausas y algunos otros eventos.

        .row.justify-content-around.align-items-center
          .col-lg-3.col-8.mb-2.mb-lg-0
            figure
              img(src='@/assets/curso/temas/t4/t4_22.png', alt='')
          .col-lg-8
            h4 SocialBlade
            p.mb-0 Obtiene informacin sobre el rendimiento de competidores o socios potenciales, por ejemplo, puede comparar la audiencia de hasta tres canales de YouTube, obteniendo los datos en bruto ms importantes y otros aspectos como proyecciones futuras en grficos.

        .row.justify-content-around.align-items-center
          .col-lg-3.col-8.mb-2.mb-lg-0
            figure
              img(src='@/assets/curso/temas/t4/t4_23.png', alt='')
          .col-lg-8
            h4 vidIQ
            p.mb-0 Es una extensin de Chrome con la que se puede analizar un canal de YouTube y brindar informacin significativa con la que poder tomar decisiones procesables, por ejemplo, ofrece un anlisis de los videos para conocer cmo interactan los usuarios entre s.

    separador

    #t_4_3.titulo-segundo.color-acento-botones(data-aos="fade-right") 
      h2 4.3 Sonido
    
    .row.justify-content-center.mb-5
      .col-lg-4.col-8.mb-4.mb-lg-0(data-aos="zoom-in")
        figure
          img(src="@/assets/curso/temas/t4/t4_24.svg", alt="alt")
      .col-lg-8(data-aos="fade-right")
        p Cuando se habla de sonido se refiere a la propagacin de las ondas mecnicas originadas por la vibracin de un cuerpo a travs de un fluido o un medio elstico. Dichas ondas pueden o no ser percibidas por los seres vivos, dependiendo de su frecuencia.
        .row
          .col-auto
            figure
              img(src="@/assets/curso/temas/recursos/rectangle_01.svg", alt="alt")
          .col
            p.mb-0 #[strong Qu consigue el sonido en la produccin audiovisual?]

    .row.justify-content-center.mb-5
      .col-lg-4.col-md-10.col-12.mb-4.mb-lg-0(data-aos="flip-left")
        .crd.bg--paleBlue.brad_01.hg-100.p-4
          figure.mb-3
            img(src="@/assets/curso/temas/t4/t4_25.svg", alt="alt").img75
          p.mb-0 #[strong Crear un ambiente:] es importante enmarcar las imgenes dentro de un ambiente adecuado en el que encajen y que, por supuesto, persuadan a continuar disfrutando del producto creado. El medio persuade a la interaccin y es importante que este sea perfecto, que se encuentre equilibrado en todos sus elementos, incluido el sonido.

      .col-lg-4.col-md-10.col-12.mb-4.mb-lg-0(data-aos="flip-left")
        .crd.bg--paleYellow.brad_01.hg-100.p-4
          figure.mb-3
            img(src="@/assets/curso/temas/t4/t4_26.svg", alt="alt").img75
          p #[strong Captar la atencin:] hay varios temas en un audiovisual que requieren de un anlisis detallado. Pueden ser aquellos en los que se da el mensaje principal, el comienzo o el final de la produccin o simplemente cualquier tema a resaltar.

          p.mb-0 La diferencia de volumen, la incorporacin de melodas diferentes e incluso el cambio de tono de la escena servirn para captar la atencin del espectador.

      .col-lg-4.col-md-10.col-12.mb-4.mb-lg-0(data-aos="flip-left")
        .crd.bg--paleGray.brad_01.hg-100.p-4
          figure.mb-3
            img(src="@/assets/curso/temas/t4/t4_27.svg", alt="alt").img75
          p.mb-0 #[strong Crear sensaciones:] es importante que, para ser recordados, se creen diferentes sensaciones, pero no pueden ser sensaciones aleatorias, es necesario estudiar al pblico y definir unos objetivos claros y partiendo de eso, crear melodas e incluir sonidos para hacer que persuada la sensacin buscada.

    .row.mb-4(data-aos="fade-right")
