        h3.custom-H3 Edicin

    figure(data-aos="zoom-in")
      img(src="@/assets/curso/temas/t4/t4_28.png", alt="alt")
    .row.justify-content-center.mb-5.mb-lg-0(data-aos="fade")
      .col-lg-10
        .cstmTitleCrd
          p.mb-0 En la edicin audiovisual hoy en da se cuenta con un sin nmero de programas diseados para la edicin de audio, con herramientas que claramente ayudan a que un producto sea ptimo; la tecnologa brinda muchas opciones. A continuacin, se describen algunos de los programas ms comunes:

    TabsC.color-acento-botones.mb-5(data-aos="zoom-in")
      .py-3.py-md-4(titulo="Audicity")
        .row.justify-content-center.align-items-center 
          .col-lg-4.col-8.order-2.order-lg-1
            figure
              img(src="@/assets/curso/temas/t4/t4_29.png", alt="alt") 

          .col-lg-8.order-1.order-lg-2.mb-4.mb-lg-0.dot-left
            h4 Audicity
            p Fue creado en el ao 2000 y sigue siendo uno de los programas de edicin de audio ms conocidos que existen. Es un #[em software] gratuito, de cdigo abierto y multiplataforma que es muy fcil de usar para cualquier persona sin experiencia previa en edicin de audio. Estas son algunas de las muchas cosas que puede hacer con l:           
            ul.fa-ul
              li
                i.fa-li.fas.fa-caret-right.clr--orange
                | Grabar audio.
              li
                i.fa-li.fas.fa-caret-right.clr--orange
                | Digitalizar grabaciones de otros medios.
              li
                i.fa-li.fas.fa-caret-right.clr--orange
                | Editar y agregar efectos a su audio, como reverberacin, autoajuste, distorsin y eco.
              li
                i.fa-li.fas.fa-caret-right.clr--orange
                | Habilitar los atajos de teclado para un flujo de trabajo ms rpido.
              li
                i.fa-li.fas.fa-caret-right.clr--orange
                | Ver audio en modo espectrograma para seleccionarlo y visualizarlo en frecuencias.
              li
                i.fa-li.fas.fa-caret-right.clr--orange
                | Agregar complementos como LV2 y VST para mejorar an ms su audio.
              li
                i.fa-li.fas.fa-caret-right.clr--orange
                | Importar / exportar audios de 16 #[em bits], 24 #[em bits] y 32 #[em bits] para obtener una excelente calidad de sonido.
              li
                i.fa-li.fas.fa-caret-right.clr--orange
                | Audacity es compatible con varios sistemas operativos, incluidos Windows, MacOS y Linux.

      .py-3.py-md-4(titulo="Adobe Audition")
        .row.justify-content-center.align-items-center 
          .col-lg-4.col-8.order-2.order-lg-1
            figure
              img(src="@/assets/curso/temas/t4/t4_30.png", alt="alt") 

          .col-lg-8.order-1.order-lg-2.mb-4.mb-lg-0.dot-left
            h4 Adobe Audition
            p Es un programa muy especial a la hora de editar audios y obtener un sonido impecable mezclndolo, limpiando y, literalmente, ajustndolo, todo mientras tiene un flujo de trabajo fluido.

            p Su pantalla y su panel de sonido esencial lo ayudan a restaurar los audios y lograr una calidad de nivel profesional, incluso si no es un profesional en lo que hace. Sin mencionar que tambin puede hacer remezclas de audio reorganizando las pistas para que se ajusten a cualquier duracin. Si su audio existente tiene problemas, puede solucionarlo y sus herramientas integradas, como el panel de diagnstico, la reduccin de audio, DeClipper y DeHummer.

            p Adobe Audition est hecho para varios propsitos de utilizacin de audio, incluidos podcasts, que exigen un sonido muy ntido hoy en da. Por lo tanto, puede visualizar claramente qu tan buenos sern sus audios editados con este #[em software].

      .py-3.py-md-4(titulo="WavePad")
        .row.justify-content-center.align-items-center 
          .col-lg-4.col-8.order-2.order-lg-1
            figure
              img(src="@/assets/curso/temas/t4/t4_31.png", alt="alt") 

          .col-lg-8.order-1.order-lg-2.mb-4.mb-lg-0.dot-left
            h4 WavePad
            p Es otro editor de audio importante que tiene una variedad de funciones bsicas y avanzadas. Puede usarlo para todos sus propsitos de edicin de audio, incluida la produccin de msica y grabaciones regulares.

            p Es compatible con los principales formatos de audio y le permite cortar, copiar, recortar, dividir, amplificar, ecualizar y normalizar sus pistas de audio en cuestin de segundos. Si es alguien que produce muchas bandas sonoras o cualquier audio y quiere ahorrar tiempo en el proceso de edicin, puede utilizar su funcin de procesamiento por lotes para editarlos todos a la vez. Es muy til para las personas que estn ocupadas y quieren algo rpido.

            p.mb-0 WavePad se puede descargar de forma gratuita en Windows, Mac, IOS, Android y Chromebook con funciones limitadas. Para desbloquear todo el potencial de este #[em software] debe elegir su versin paga.

      .py-3.py-md-4(titulo="TwistedWave")
        .row.justify-content-center.align-items-center 
          .col-lg-4.col-8.order-2.order-lg-1
            figure
              img(src="@/assets/curso/temas/t4/t4_32.png", alt="alt") 

          .col-lg-8.order-1.order-lg-2.mb-4.mb-lg-0.dot-left
            h4 TwistedWave
            p Es un programa que tiene la capacidad de grabar, masterizar, convertir y procesar por lotes su audio. TwistedWave es una solucin todo en uno para todo lo relacionado con los audios, y lo mejor, es muy fcil de usar para cualquier persona. Adems de las caractersticas habituales, aqu hay algunas cosas nicas que puede hacer con este #[em software]:           
            ul.fa-ul
              li
                i.fa-li.fas.fa-caret-right.clr--orange
                | Detecta automticamente puntos de silencio en la pista de audio para dividirlos fcilmente.
              li
                i.fa-li.fas.fa-caret-right.clr--orange
                | Corrija el tono o acelere de forma independiente.
              li
                i.fa-li.fas.fa-caret-right.clr--orange
                | Cree tonos de llamada de iPhone fcilmente y exprtelos en un formato compatible.
              li
                i.fa-li.fas.fa-caret-right.clr--orange
                | Trabaje en sus pistas de audio actuales mientras sus archivos grandes se cargan en segundo plano.
              li
                i.fa-li.fas.fa-caret-right.clr--orange
                | Cargue cualquier cantidad de efectos de audio en su pila para aplicarlos, en lugar de tener que agregar cada uno desde cero.
              li
                i.fa-li.fas.fa-caret-right.clr--orange
                | TwistedWave es compatible con Mac e IOS, o tambin puede usarlo en lnea si no desea descargar nada.

      .py-3.py-md-4(titulo="AVS Audio Editor")
        .row.justify-content-center.align-items-center 
          .col-lg-4.col-8.order-2.order-lg-1
            figure
              img(src="@/assets/curso/temas/t4/t4_33.png", alt="alt") 

          .col-lg-8.order-1.order-lg-2.mb-4.mb-lg-0.dot-left
            h4 AVS Audio Editor
            p Ha sido creado nicamente para Windows, admite todos los formatos de audio principales y le facilita la creacin de pistas brillantes. Puede cortar, recortar, dividir, fusionar y manipular sus audios en gran medida, incluida la capacidad de mezclar diferentes pistas juntas. Tambin hay una biblioteca de efectos, como reverberacin, coro, flanger y eco, que se pueden aplicar instantneamente a los puntos deseados de la pista. Adems de eso, puede:           
            ul.fa-ul
              li
                i.fa-li.fas.fa-caret-right.clr--orange
                | Extraer audio de archivos de video.
              li
                i.fa-li.fas.fa-caret-right.clr--orange
                | Grabar audio desde varias entradas como micrfono y discos de vinilo.
              li
                i.fa-li.fas.fa-caret-right.clr--orange
                | Eliminar los ruidos de fondo innecesarios.
              li
                i.fa-li.fas.fa-caret-right.clr--orange
                | Cambiar la tasa de #[em bits] del sonido a cualquier nivel.
              li
                i.fa-li.fas.fa-caret-right.clr--orange
                | Convertir el texto escrito en una voz que suene natural, todo gracias a su funcin de texto a voz. 

            p.mb-0 Dado que este #[em software] solo est disponible para Windows, puede descargarlo en las versiones Vista, 2003, XP, 7, 8, 8.1 y 10.

      .py-3.py-md-4(titulo="Logic Pro")
        .row.justify-content-center.align-items-center 
          .col-lg-4.col-8.order-2.order-lg-1
            figure
              img(src="@/assets/curso/temas/t4/t4_34.png", alt="alt") 

          .col-lg-8.order-1.order-lg-2.mb-4.mb-lg-0.dot-left
            h4 Logic Pro
            p De Apple le permite implementar algunas de las prcticas de edicin y produccin de audio ms exigentes. Puede utilizar sus funciones #[em Step Sequencer, Sampler y Quick Sampler] para crear ritmos de batera, patrones meldicos y transformar sonidos en instrumentos, todo en un santiamn.
            p.mb-0 Adems de eso, puede agregar efectos de estilo DJ, tocar cualquier instrumento (guitarra, batera, teclado, etc.) grabar audio ntido, agregar nueva msica a la pista existente sin alterar el tiempo actual y realizar la correccin de tono. Si usa iPhone o iPad puede utilizar la funcin #[em Logic Remote] para controlar ciertas funciones como #[em Live Loops] y #[em Remix FX] directamente desde su dispositivo con gestos multitctiles.

    .row.mb-4(data-aos="fade-right")
      .col-auto
        h3.custom-H3 Optimizacin

    .squareTop(data-aos="zoom-in").mb-4
      p Para optimizar el peso del archivo de audio que se va a subir a la #[em web] ser necesario utilizar un editor para reducir alguno o algunos de los siguientes parmetros:

    .row.justify-content-center.mb-5
      .col-lg-7(data-aos="fade-right")
        LineaTiempoD.color-acento-botones.mb-5
          .row(numero="1" titulo="Tasa de muestreo")
            .col
              p.mb-0 Definir valores inferiores: 44100 Hz., 22050 Hz., 11025 Hz, etc.

          .row(numero="2" titulo="Resolucin")
            .col
              p.mb-0 Establecer resoluciones ms pequeas: 32-#[em bits], 16-#[em bits], 8-#[em bits], 4-#[em bits], etc.

          .row(numero="3" titulo="Duracin")
            .col
              p.mb-0 En ocasiones se puede utilizar un fragmento ms corto que reproducido en bucle cubre el tiempo suficiente de acompaamiento musical, a estos se les llama #[em loops].

          .row(numero="4" titulo="Calidad estreo/mono")
            .col
              p.mb-0 La reduccin a calidad mono reduce considerablemente el peso del archivo, esta calidad para la mayora de los audios y de usuarios es apenas perceptible.

          .row(numero="5" titulo="Formato")
            .col
              p.mb-0 Es preferible utilizar el formato MP3 u OGG en lugar del WAV por su potente factor de compresin y su aceptable calidad de audio. 

      .col-lg-5.col-8(data-aos="zoom-in")
        figure
          img(src="@/assets/curso/temas/t4/t4_35.svg", alt="alt")

    .row.mb-4(data-aos="fade-right")
      .col-auto
        h3.custom-H3 Formato de salida

    .squareTop.mb-4(data-aos="fade-right")
      p.mb-0 Identificar en qu formatos finales se archivan los audios permitir saber cul es el que mejor se adapta a las necesidades, conozca los ms comunes:

    AcordionA(tipo="a" clase-tarjeta="tarjeta tarjeta--gris")(data-aos="zoom-in").mb-5
      .row.justify-content-center(titulo="Formato WAV")
        .col-lg-8.mb-4.mb-lg-0
          p Wave Form Audio File es un archivo que desarroll originalmente Microsoft para guardar audio. Los archivos tienen extensin .WAV.
          p Es ideal para guardar audios originales a partir de los cuales se puede comprimir y guardar en distintos tamaos de muestreo para publicar en la #[em web].
          p.mb-0 Es un formato de excelente calidad de audio; sin embargo, produce archivos de un peso enorme.

        .col-lg-4.col-10
          figure
            img(src="@/assets/curso/temas/t4/t4_36.png", alt="alt")

        .col-12.mt-4
          p.mb-0 Los archivos WAV se pueden guardar con distintos tipos de compresin. Las ms utilizadas son la compresin PCM y la compresin ADPCM. No obstante, incluso definiendo un sistema de compresin, con un audio de cierta duracin se genera un archivo excesivamente pesado.

      .row.justify-content-center(titulo="Formato MP3")
        .col-lg-4.col-10.mb-4.mb-lg-0
          figure
            img(src="@/assets/curso/temas/t4/t4_37.png", alt="alt")
        .col-lg-8
          p.mb-0 MPEG 1 Layer 3 fue creado por el Instituto Fraunhofer y por su extraordinario grado de compresin y alta calidad ha monopolizado el mundo del audio digital. Los archivos tienen extensin .mp3. Es ideal para publicar audios en la #[em web]. Se puede escuchar desde la mayora de reproductores. La transformacin de WAV a MP3 o la publicacin directa de una grabacin en formato MP3 es un proceso fcil y al alcance de los principales editores de audio. Tiene un enorme nivel de compresin respecto al WAV. En igualdad del resto de condiciones reducira el tamao del archivo de un fragmento musical con un factor entre 1/10 y 1/12. Presentan una mnima prdida de calidad.

      .row.justify-content-center(titulo="Formato OGG")        
        .col-lg-8.mb-4.mb-lg-0
          p.mb-0 Desarrollado por la fundacin Xiph.org, es libre y de cdigo abierto, a diferencia del formato MP3. Los archivos tienen extensin .ogg. Muestra un grado de compresin similar al MP3 pero segn los expertos en msica la calidad de reproduccin es ligeramente superior. No todos los reproductores multimedia son capaces de leer por defecto este formato. En algunos casos es necesario instalar los cdec o filtros oportunos. El formato OGG puede contener audio y video.
        .col-lg-4.col-10
          figure
            img(src="@/assets/curso/temas/t4/t4_38.png", alt="alt")

      .row.justify-content-center(titulo="Formato MIDI")  
        .col-12.mb-4
          p Musical Instrument Digital en espaol Interfaz Digital para Instrumentos Digitales, en realidad no resulta de un proceso de digitalizacin de un sonido analgico y, por ello, no es un formato de audio propiamente dicho. Sus caractersticas son las siguientes:

        .col-lg-8.mb-4.mb-lg-0
          ul.fa-ul
            li
              i.fa-li.fa.fa-check-square.clr--orange
              | Un archivo de extensin *.mid almacena secuencias de dispositivos MIDI (sintetizadores) donde se recoge qu instrumento interviene, en qu forma lo hace y cundo los archivos MIDI se pueden editar y manipular mediante programas especiales y distintos de los empleados para editar formatos WAV, MP3, etc. El manejo de estos programas suele conllevar ciertos conocimientos musicales.
            li
              i.fa-li.fa.fa-check-square.clr--orange
              | Los archivos MIDI permiten audios de cierta duracin con un reducido peso. Esto es debido a que no guardan el sonido sino la informacin o partitura necesaria para que el ordenador la componga y reproduzca a travs de la tarjeta de sonido. Se suelen utilizar en sonidos de fondo de pginas HTML o para escuchar composiciones musicales de carcter instrumental.

        .col-lg-4.col-10
          figure
            img(src="@/assets/curso/temas/t4/t4_39.png", alt="alt")

    separador    

    #t_4_4.titulo-segundo.color-acento-botones(data-aos="fade-right") 
      h2 4.4 Transmedia

    .row.justify-content-center.mb-4
      .col-lg-4.col-8.mb-4.mb-lg-0(data-aos="zoom-in")
        figure
          img(src="@/assets/curso/temas/t4/t4_40.svg", alt="alt")
      .col-lg-8(data-aos="fade-right")
        .squareTop.mb-4
          p.mb-0 La narrativa transmedia es una tcnica en la cual una historia se desarrolla o divide en diferentes plataformas para formar un relato coherente, es decir, podra entenderse como una historia contada en captulos que se encuentran en diferentes formatos: libro, #[em post], #[em spot], pelcula.
        .row
          .col-auto
            figure
              img(src="@/assets/curso/temas/recursos/square_01.svg", alt="alt").img30
          .col
            p.mb-0 El trmino transmedia no hace referencia al empleo de diversos medios como canales de difusin; se trata de hacer crecer y expandir una historia, sumando las piezas nicas de la narrativa expresadas en cada medio. Es importante entender que no se trata de crear una historia y difundirla por varios medios para su difusin, se trata es de involucrar a la audiencia para que logre un papel protagnico en su proceso.

    p(data-aos="fade-right") El desarrollo de la historia implica la creacin de contenido de acuerdo con los canales donde el consumidor dar seguimiento. El punto clave es que la historia y el contenido se relacione entre s para otorgar sincrona a la narrativa presentada. 

    h5(data-aos="fade-right").mb-5 Algunas tendencias en este tipo de narrativa son las siguientes:

    .row.justify-content-center.mb-5
      .col-xl-2.col-lg-4.col-8.mb-4.mb-xl-0(data-aos="flip-left")
        .crd.bg--gray.brad_02.shadow.p-3.hg-100
          .sm-numb(sm-numb-data="01")
          figure.mb-3
            img(src="@/assets/curso/temas/t4/t4_41.svg", alt="alt").img90.m-auto
          p.text-center.mb-0 Historias interactivas.
      .col-xl-2.col-lg-4.col-8.mb-4.mb-xl-0(data-aos="flip-left")
        .crd.bg--gray.brad_02.shadow.p-3.hg-100
          .sm-numb(sm-numb-data="02")
          figure.mb-3
            img(src="@/assets/curso/temas/t4/t4_42.svg", alt="alt").img90.m-auto
          p.text-center.mb-0 Integracin de tecnologa como realidad virtual.
      .col-xl-2.col-lg-4.col-8.mb-4.mb-xl-0(data-aos="flip-left")
        .crd.bg--gray.brad_02.shadow.p-3.hg-100 
          .sm-numb(sm-numb-data="03")
          figure.mb-3
            img(src="@/assets/curso/temas/t4/t4_43.svg", alt="alt").img90.m-auto
          p.text-center.mb-0 Narracin hiperrealista.
      .col-xl-2.col-lg-4.col-8.mb-4.mb-xl-0(data-aos="flip-left")
        .crd.bg--gray.brad_02.shadow.p-3.hg-100
          .sm-numb(sm-numb-data="04")
          figure.mb-3
            img(src="@/assets/curso/temas/t4/t4_44.svg", alt="alt").img90.m-auto
          p.text-center.mb-0 Historias conectadas.
      .col-xl-2.col-lg-4.col-8.mb-4.mb-xl-0(data-aos="flip-left")
        .crd.bg--gray.brad_02.shadow.p-3.hg-100
          .sm-numb(sm-numb-data="05")
          figure.mb-3
            img(src="@/assets/curso/temas/t4/t4_45.svg", alt="alt").img90.m-auto
          p.text-center.mb-0 Historias inmersivas.

    .cajon.cjn--grayBlue.p-4.mb-5(data-aos="zoom-in")
      p.mb-0 La creacin de contenidos audiovisuales se ha venido transformando, con el crecimiento de las nuevas tecnologas, estas han tomado importancia e inters en el mundo y la manera de utilizarlas ha evolucionado rpidamente, a consecuencia de la #[em transmedia storylling] o confluencia de medios.


    .row.justify-content-center.mb-5
      .col-lg-8.mb-4.mb-lg-0(data-aos="fade-right")
        .squareTop
          p El consumo de informacin de los espectadores en consecuencia ha cambiado de quin produce a quin consume, hacia una cultura participativa. Esto en gran parte a la gran demanda de los dispositivos mviles y todas las funciones con las que estos cuentan y la Internet. 
        .row
          .col-auto.d-none.d-lg-flex
            figure
              img(src="@/assets/curso/temas/recursos/square_01.svg", alt="alt")
          .col
            p.mb-0 De esta forma, cuando recrea una produccin audiovisual transmedia se pretende que sea un rol de relato donde la historia se despliega por varios medios y medios de comunicacin. Las redes sociales, la Internet y sitios #[em web] hacen parte de esta forma de producciones, ya que permiten la interaccin y participacin del pblico.
      .col-lg-4.col-8(data-aos="zoom-in")
        figure
          img(src="@/assets/curso/temas/t4/t4_46.svg", alt="alt")

    .tarjeta.color-secundario.p-3.mb-5(data-aos="zoom-in")
      .row.justify-content-around.align-items-center
        .col-auto
          img(src="@/assets/curso/temas/recursos/cta_02.svg").img90
        .col
          .row.justify-content-between.align-items-center
            .col.mb-3.mb-sm-0
              h3.mb-1 Ejemplo de produccin transmedia
              p.text-small Estimado aprendiz, Se recomienda ver el siguiente video de produccin transmedia.
